<svelte:options tag="fds-image-editor-selection-floating-toolbar"></svelte:options>
<script>

/**
 * the tool layer object
 * @type {object}
 */   
export let tool_layer
export function refresh() {
    if (!globalThis.gyre.paletteValues.smoothSelection) globalThis.gyre.paletteValues.smoothSelection=0
    smoothSliderValue=globalThis.gyre.paletteValues.smoothSelection
    if (!tool_layer) return
    if (!tool_layer.mode) tool_layer.mode="add"
    tool_layer=tool_layer
}
function smoothSelection() {
    globalThis.gyre.paletteValues.smoothSelection=smoothSliderValue=sliderElement.value
}
function openMenu() {
    let contextMenu = window.document.createElement("fds-image-editor-menu")
    let tool=globalThis.gyre.selectedTool.subTool
    contextMenu.menu = { }
    if (tool==="rectangle" || tool==="ellipse") {
        contextMenu.menu = { style:{name:"Style...",key:"",items:{
            normal:{name:"Normal",key:"normal"},
            fixed_ratio:{name:"Fixed Ratio",key:"fixed_ratio"},
            fixed_size:{name:"Fixed Size",key:"fixed_size"},
        }}}
    }
    contextMenu.menu={...contextMenu.menu, ...{
        inpainting_mask:{name:"Inpainting Mask",key:"inpainting_mask"},
        mask:{id:6,name:"Layer Mask",key:"mask"},
        linked_layer_mask:{id:7,name:"New Linked-Layer Mask",key:"linked_layer_mask"},
        clear:{id:8,name:"Clear",key:"clear"},
        deselect:{id:9,name:"Deselect",key:"deselect"},
        invert:{id:10,name:"Invert",key:"invert"},
        crop:{id:11,name:"Crop",key:"crop"},
    }}
    contextMenu.element = hamburgerMenu                    
    contextMenu.callback = async (item) => {
        tool_layer.component.action(item.key)
    }
    window.document.body.append(contextMenu)
}
let hamburgerMenu,sliderElement
let smoothSliderValue=0

let dropdownRatio
function updateRatio(value) {
    if (!value || value==="select") return
    if (value==="document") {
        tool_layer.ratio_width=globalThis.gyre.canvas.width
        tool_layer.ratio_height=globalThis.gyre.canvas.height
    } else {
        let arr=value.split(":")
        tool_layer.ratio_width=arr[0]
        tool_layer.ratio_height=arr[1]
    }
    tool_layer=tool_layer
    tool_layer.component.action("update_ratio")
    dropdownRatio.value="select"
}

</script>
{#if tool_layer}

       <!-- svelte-ignore a11y-click-events-have-key-events -->
       <fds-image-editor-button
       style="margin-left:10px"
       icon="plusCircleDotted"
       class="icon"
       state={tool_layer.mode === 'add' ? 'active' : ''}
       on:click={() => {
           tool_layer.component.action('add')
       }}
   ></fds-image-editor-button>
          <!-- svelte-ignore a11y-click-events-have-key-events -->
          <fds-image-editor-button
          style="margin-left:10px"
          icon="minusCircleDotted"
          class="icon"
          state={tool_layer.mode === 'subtract' ? 'active' : ''}
          on:click={() => {
              tool_layer.component.action('subtract')
          }}
      ></fds-image-editor-button>
          <!-- svelte-ignore a11y-click-events-have-key-events -->
          <fds-image-editor-button
          style="margin-left:10px"
          icon="moveTool"
          state={tool_layer.mode === 'move' ? 'active' : ''}
          class="icon"
          on:click={() => {
              tool_layer.component.action('move')
          }}
      ></fds-image-editor-button>      
          <!-- svelte-ignore a11y-click-events-have-key-events -->
          <fds-image-editor-button
          style="margin-left:10px"
          icon="hamburgerMenu"
          class="icon"
          bind:this={hamburgerMenu}
          on:click={() => { openMenu()      }}
      ></fds-image-editor-button>    
      Smoothness:
      <fds-image-editor-slider
      min="0"
      max="50"
      bind:this={sliderElement}
      on:input={smoothSelection}
      value={smoothSliderValue}
      width="150px"
      class="slider"
/>
    {#if tool_layer.style==="fixed_ratio"} 
          <div class="sizeInput">
        Fixed Ratio: <input type="number" class="formInput" style="width:60px" bind:value={tool_layer.ratio_width} on:change={() => {  tool_layer.component.action("update_ratio") }}>:<input type="number" class="formInput" style="width:60px" bind:value={tool_layer.ratio_height} on:change={() => {  tool_layer.component.action("update_ratio") }}>
        <select class="formInput dropDown" on:change={(e) => { updateRatio(e.target.value) }} bind:this={dropdownRatio} style="width:150px">
            <option value="select">Select...</option> 
            <option value="1:1">1:1</option>
            <option value="16:9">16:9</option>
            <option value="9:16">9:16 (TikTok)</option>
            <option value="4:3">4:3</option>
            <option value="3:2">3:2</option>
            <option value="1000:618">Golden Ratio</option>
            <option value="618:1000">Golden Ratio upright</option>        
            <option value="document">Document</option>

        </select>
    </div>
    {/if}
    {#if tool_layer.style==="fixed_size"} 
    <div class="sizeInput">

        Width: <input type="number" class="formInput" style="width:60px" bind:value={tool_layer.fixed_width} on:change={() => {  tool_layer.component.action("update_fixed") }}>, Height: <input type="number" class="formInput" style="width:60px" bind:value={tool_layer.fixed_height} on:change={() => {  tool_layer.component.action("update_fixed") }}>
    </div>

    {/if}

{/if}
<style>

    .icon {
        vertical-align: -10px;display: inline-block; 
    }
    .slider {
        vertical-align: -2px; 

    }
    .formInput {
        width: 100%;
        max-width: 80px;
        outline: 0;
        border: 0;
        border-bottom: 2px solid rgba(255, 255, 255, 0.2);
        padding: 5px;
        font-size: 14px;
        font-weight: normal;
        border-radius: 3px;
        color: rgba(255, 255, 255, 0.9);
        margin-bottom: 10px;
        display: inline-block;
        background: none;
        font-family: system-ui, "Segoe UI", Roboto;

    }    
    .dropDown {
        font-weight: normal;
        background-color: rgba(0, 0, 0, 0.65);
        backdrop-filter: blur(10px);
    }
    .sizeInput {
        margin-left:10px;
    }
</style>
