// largePaper.js
import paper from 'paper';

export class LargePaper {
    onDimensionsChange
    constructor(screenCanvas, documentWidth, documentHeight, initialZoom = 1,callback) {
        this.documentWidth = documentWidth;
        this.documentHeight = documentHeight;
        this.screenCanvas = screenCanvas;
        this.zoomFactor = initialZoom;
        this.offsetX = documentWidth / 2;
        this.offsetY = documentHeight / 2;

    //    this.currentToolName = 'freeform';
        this.currentMode = 'add'; // 'add' or 'subtract'
        this.currentTool = null;
        this.callback=callback
        this.setupPaper();
    }

setupPaper() {
    paper.clear()

    // Adjust for device pixel ratio
    const dpr = window.devicePixelRatio || 1
    paper.setup(this.screenCanvas)

    // Scale the context
    paper.view.scale(dpr, dpr)

    // Create layers
    this.internalLayer = new paper.Layer() // Holds original paths, not visible
    this.internalLayer.name = 'internalLayer';

    this.renderedLayer = new paper.Layer() // Holds rendered paths, displayed
    this.renderedLayer.name = 'renderedLayer';

    this.selectionLayer = new paper.Layer()
    this.selectionLayer.name = 'selectionLayer';

    // Ensure the correct layer order
    this.internalLayer.visible = false; // Hide the internal layer
    this.renderedLayer.bringToFront()
    this.selectionLayer.bringToFront()

    // Initialize the view
    this.updateView()

    // Initialize tools
    this.setTool(this.currentToolName)

    // Start the animation loop for dash offset
    paper.view.onFrame = () => {
        this.animatePaths()
    }
}

    updateView() {
        // Set the view's zoom factor
        paper.view.zoom = this.zoomFactor;

        // Set the view's center
      //  paper.view.center = new paper.Point(this.offsetX, this.offsetY);

        // Update rendered paths
        this.updateRenderedPaths();
    }

    setZoom(zoomFactor) {
        this.zoomFactor = zoomFactor;
        this.updateView();
        paper.view.update();
    }

    pan(X, Y) {
        // Adjust the offsetX and offsetY according to the delta, divided by the current zoom factor
        this.offsetX = X / this.zoomFactor;
        this.offsetY = Y / this.zoomFactor;
        this.updateView();
        paper.view.update();
    }

    setMode(mode) {
        this.currentMode = mode; // 'add', 'subtract', or 'move'
        if (mode === 'move') {
            this.setTool('move');
        } else {
            // When switching to 'add' or 'subtract', reactivate the last used tool
            this.setTool(this.currentToolName);
        }
    }

    setTool(toolName,options={}) {

        if (toolName !== 'move') {
            this.currentToolName = toolName; // Update currentToolName only for non-move tools
        }
        
        if (this.currentTool) {
            this.currentTool.remove();
        }

        this.currentTool = new paper.Tool();
        switch (toolName) {
            case 'rectangle':
                this.initRectangleTool(options)
                break;
            case 'ellipse':
                this.initEllipseTool(options)
                break;
            case 'freeform':
                this.initFreeformTool();
                break;
            case 'move':
                this.initMoveTool();
                break;
            default:
                break;
        }
    }

    initMoveTool() {
        if (!this.currentTool) return
        this.currentTool.onMouseDown = (event) => {
            this.isDragging = true;
            this.lastDragPoint = event.point;
        };

        this.currentTool.onMouseDrag = (event) => {
            if (this.isDragging) {
                const delta = event.point.subtract(this.lastDragPoint);
                this.moveAllPaths(delta);
                this.lastDragPoint = event.point;
            }
        };

        this.currentTool.onMouseUp = () => {
            this.isDragging = false;
            this.lastDragPoint = null;
        };
    }

    moveAllPaths(delta) {
        if (!this.internalLayer) return
        this.internalLayer.children.forEach(path => {
            path.position = path.position.add(delta)
        });
        this.updateRenderedPaths()
    }

    normalizePoint(point) {
        return point;
    }

    initRectangleTool(options = {}) {
        let path;
        let startPoint;
        let aspectRatio = options.aspectRatio || null;
        let fixedSize = options.fixedSize || null;
        let isDragging = false;
        let offset;
    
        this.currentTool.onMouseDown = (event) => {
            startPoint = this.normalizePoint(event.point);
            // Reset dimensions
            this.currentWidth = fixedSize ? fixedSize.width : 0;
            this.currentHeight = fixedSize ? fixedSize.height : 0;
            if (fixedSize) {
                // Create a rectangle of fixed size at the startPoint
                const rectangle = new paper.Rectangle(
                    startPoint.subtract(new paper.Point(fixedSize.width / 2, fixedSize.height / 2)),
                    new paper.Size(fixedSize.width, fixedSize.height)
                );
                path = new paper.Path.Rectangle(rectangle);
                this.setPathStyle(path);
                if (!this.selectionLayer) return;
                this.selectionLayer.addChild(path);
                isDragging = true;
                offset = event.point.subtract(path.position);
            } else {
                // Normal rectangle drawing
                path = new paper.Path.Rectangle(startPoint, startPoint);
                this.setPathStyle(path);
                if (!this.selectionLayer) return;
                this.selectionLayer.addChild(path);
            }
        };
    
        this.currentTool.onMouseDrag = (event) => {
            this.isDragging=true
            if (fixedSize && isDragging) {
                // Move the rectangle with the mouse, maintaining the offset
                path.position = event.point.subtract(offset);
            } else {
                const endPoint = this.normalizePoint(event.point);
                path.remove();
                // Calculate dimensions
                this.currentWidth = Math.abs(endPoint.x - startPoint.x);
                this.currentHeight = Math.abs(endPoint.y - startPoint.y);
                if (aspectRatio) {
                    // Aspect ratio logic as before
                    const deltaX = endPoint.x - startPoint.x;
                    const deltaY = endPoint.y - startPoint.y;
    
                    let width, height;
                    if (Math.abs(deltaX) >= Math.abs(deltaY * aspectRatio)) {
                        width = deltaX;
                        height = (deltaX / aspectRatio) * (deltaY < 0 ? -1 : 1);
                    } else {
                        height = deltaY;
                        width = (deltaY * aspectRatio) * (deltaX < 0 ? -1 : 1);
                    }
    
                    const adjustedEndPoint = new paper.Point(startPoint.x + width, startPoint.y + height);
                    path = new paper.Path.Rectangle(startPoint, adjustedEndPoint);
 
                    this.currentWidth = Math.abs(width);
                    this.currentHeight = Math.abs(height);                    
                } else {
                    // No aspect ratio constraint
                    path = new paper.Path.Rectangle(startPoint, endPoint);
                }
                this.setPathStyle(path);
                if (!this.selectionLayer) return;
                this.selectionLayer.addChild(path);
            }
        };
    
        this.currentTool.onMouseUp = () => {
            this.processPath(path);
            path = null;
            isDragging =this.isDragging= false;
            if (!this.selectionLayer) return;
            this.selectionLayer.removeChildren(); // Clear selection layer
        };
    }
    

    initEllipseTool(options = {}) {
        let path;
        let startPoint;
        let aspectRatio = options.aspectRatio || null;
        let fixedSize = options.fixedSize || null;
        let isDragging = false;
        let offset;
    
        this.currentTool.onMouseDown = (event) => {
            startPoint = this.normalizePoint(event.point);
            this.currentWidth = fixedSize ? fixedSize.width : 0;
            this.currentHeight = fixedSize ? fixedSize.height : 0;
            if (fixedSize) {
                // Create an ellipse of fixed size at the startPoint
                const rectangle = new paper.Rectangle(
                    startPoint.subtract(new paper.Point(fixedSize.width / 2, fixedSize.height / 2)),
                    new paper.Size(fixedSize.width, fixedSize.height)
                );
                path = new paper.Path.Ellipse(rectangle);
                this.setPathStyle(path);
                if (!this.selectionLayer) return;
                this.selectionLayer.addChild(path);
                isDragging = true;
                offset = event.point.subtract(path.position);
            } else {
                // Normal ellipse drawing
                path = new paper.Path.Ellipse({
                    rectangle: new paper.Rectangle(startPoint, startPoint),
                });
                this.setPathStyle(path);
                if (!this.selectionLayer) return;
                this.selectionLayer.addChild(path);
            }
        };
    
        this.currentTool.onMouseDrag = (event) => {
            this.isDragging=true
            if (fixedSize && isDragging) {
                // Move the ellipse with the mouse, maintaining the offset
                path.position = event.point.subtract(offset);
            } else {
                const endPoint = this.normalizePoint(event.point);
                path.remove();
    
                let rectStartPoint = startPoint;
                let rectEndPoint = endPoint;
                // Calculate dimensions
                this.currentWidth = Math.abs(endPoint.x - startPoint.x);
                this.currentHeight = Math.abs(endPoint.y - startPoint.y);
                if (aspectRatio) {
                    // Adjust the rectangle used to define the ellipse to maintain aspect ratio
                    const deltaX = endPoint.x - startPoint.x;
                    const deltaY = endPoint.y - startPoint.y;
    
                    let width, height;
                    if (Math.abs(deltaX) >= Math.abs(deltaY * aspectRatio)) {
                        width = deltaX;
                        height = (deltaX / aspectRatio) * (deltaY < 0 ? -1 : 1);
                    } else {
                        height = deltaY;
                        width = (deltaY * aspectRatio) * (deltaX < 0 ? -1 : 1);
                    }
                    this.currentWidth = Math.abs(width);
                    this.currentHeight = Math.abs(height);
                    const adjustedEndPoint = new paper.Point(startPoint.x + width, startPoint.y + height);
                    rectEndPoint = adjustedEndPoint;
                }
    
                path = new paper.Path.Ellipse({
                    rectangle: new paper.Rectangle(rectStartPoint, rectEndPoint),
                });
                this.setPathStyle(path);
                if (!this.selectionLayer) return;
                this.selectionLayer.addChild(path);
            }
        };
    
        this.currentTool.onMouseUp = () => {
            this.isDragging=false
            this.processPath(path)
            path = null;
            isDragging = false;
            if (!this.selectionLayer) return;
            this.selectionLayer.removeChildren(); // Clear selection layer
        };
    }
    

    initFreeformTool() {
        let path
        if (!this.currentTool) return
        if (!this.selectionLayer) return

        this.currentTool.onMouseDown = (event) => {
            const point = this.normalizePoint(event.point)
            path = new paper.Path({
                segments: [point],
            });
            this.setPathStyle(path)
            this.selectionLayer.addChild(path)
        }

        this.currentTool.onMouseDrag = (event) => {
            let point = this.normalizePoint(event.point)
            path.add(point)
        }

        this.currentTool.onMouseUp = () => {
            path.closed = true;
            path.simplify();
            this.processPath(path);
            path = null;
            this.selectionLayer.removeChildren(); // Clear selection layer
        }
    }

    setPathStyle(path) {
        path.strokeColor = this.currentMode === 'add' ? 'green' : 'red';
        path.fillColor = new paper.Color(
            this.currentMode === 'add' ? 0 : 1,
            this.currentMode === 'add' ? 1 : 0,
            0,
            0.2
        );
        path.dashArray = [5, 5];
        path.strokeScaling = false;
    }

    processPath(path) {

        // Add the path to the internal layer
        this.internalLayer.activate();
        this.beforeShapesJSON=this.paper.project.exportJSON()
        let newPath = path.clone();
        newPath.style = path.style;
        if (this.currentMode === 'add') {
            this.mergePathAdd(newPath);
        } else if (this.currentMode === 'subtract') {
            this.mergePathSubtract(newPath);
        }

        // Update the rendered paths
        this.updateRenderedPaths();

        paper.view.update()
        this.afterShapesJSON=this.paper.project.exportJSON()

        if (this.callback) {
            this.callback()
        }
    }
    restoreJSON(json) {
        this.paper.project.clear()
        this.paper.project.importJSON(json)
        // Reassign the layer references by name
        this.internalLayer = this.paper.project.layers.find(layer => layer.name === 'internalLayer');
        this.renderedLayer = this.paper.project.layers.find(layer => layer.name === 'renderedLayer');
        this.selectionLayer = this.paper.project.layers.find(layer => layer.name === 'selectionLayer');

        this.selectionLayer.removeChildren()
        this.updateRenderedPaths()
        paper.view.update()        
    }
    mergePathAdd(newPath) {
        const existingPaths = this.internalLayer.children.filter(
            (item) => item !== newPath
        );
    
        let pathsToMerge = [newPath];
    
        // Collect all existing paths that overlap with newPath
        for (let i = 0; i < existingPaths.length; i++) {
            const existingPath = existingPaths[i];
    
            if (
                newPath.intersects(existingPath) ||
                existingPath.contains(newPath.bounds.center) ||
                newPath.contains(existingPath.bounds.center)
            ) {
                pathsToMerge.push(existingPath);
            }
        }
    
        if (pathsToMerge.length > 1) {
            // Merge all paths together
            let united = pathsToMerge[0];
    
            for (let i = 1; i < pathsToMerge.length; i++) {
                united = united.unite(pathsToMerge[i]);
            }
    
            // Remove old paths
            pathsToMerge.forEach(path => path.remove());
    
            // Add new united path to internalLayer
            this.internalLayer.addChild(united);
        } else {
            // No paths to merge, add newPath to internalLayer
            this.internalLayer.addChild(newPath);
        }
    }
    
    mergePathSubtract(newPath) {
        const existingPaths = this.internalLayer.children.filter(
            (item) => item !== newPath
        );
    
        let pathsToSubtractFrom = [];
    
        // Collect all existing paths that overlap with newPath
        for (let i = 0; i < existingPaths.length; i++) {
            const existingPath = existingPaths[i];
            if (
                newPath.intersects(existingPath) ||
                newPath.contains(existingPath.bounds.center) ||
                existingPath.contains(newPath.bounds.center)
            ) {
                pathsToSubtractFrom.push(existingPath);
            }
        }
    
        // Remove newPath from internalLayer if it's there
        if (newPath.parent) {
            newPath.remove();
        }
    
        // Subtract newPath from all overlapping existing paths
        for (let i = 0; i < pathsToSubtractFrom.length; i++) {
            const existingPath = pathsToSubtractFrom[i];
            const subtracted = existingPath.subtract(newPath);
    
            // Remove old path
            existingPath.remove();
    
            // Add subtracted path if it has content
            if (!subtracted.isEmpty()) {
                this.internalLayer.addChild(subtracted);
            }
        }
    }
    

    updateRenderedPaths() {
        // Clear the rendered layer
        this.renderedLayer.removeChildren();
    
        // Decide whether to render orthogonal paths based on zoom level
        const useOrthogonal = paper.view.zoom >= 12;
    
        // Re-render all paths from the internal layer
        this.internalLayer.children.forEach((originalItem) => {
            let renderedItem;
    
            if (originalItem instanceof paper.Path || originalItem instanceof paper.CompoundPath) {
                if (useOrthogonal) {
                    // Render orthogonal version
                    renderedItem = this.pathTo90DegreeLines(originalItem);
                } else {
                    // Render original item
                    renderedItem = originalItem.clone();
                }
    
                // Apply styles
                renderedItem.style = originalItem.style;
    
                // Add to the rendered layer
                this.renderedLayer.addChild(renderedItem);
            }
        });
    
        // Update path styles
        this.updatePathStyles();
    }
    

    pathTo90DegreeLines(path) {
        let resolution = 1; // Fixed resolution to align with image pixels
        const length = path.length;
        const points = [];
    
        // Sample the points along the path
        for (let i = 0; i <= length; i += resolution / 2) {
            points.push(path.getPointAt(i));
        }
        points.push(path.getPointAt(length)); // Add the final point
    
        const newSegments = [];
        let prevPoint = this.snapToGrid(points[0]);
        newSegments.push(prevPoint);
    
        for (let i = 1; i < points.length; i++) {
            const currentPoint = this.snapToGrid(points[i]);
    
            // Create horizontal and vertical steps between points
            if (currentPoint.x !== prevPoint.x) {
                newSegments.push(new paper.Point(currentPoint.x, prevPoint.y));
            }
            if (currentPoint.y !== prevPoint.y) {
                newSegments.push(new paper.Point(currentPoint.x, currentPoint.y));
            }
    
            prevPoint = currentPoint;
        }
    
        // Close the path properly
        const firstPoint = newSegments[0];
        const lastPoint = newSegments[newSegments.length - 1];
    
        if (!lastPoint.equals(firstPoint)) {
            newSegments.push(firstPoint.clone());
        }
    
        // Return the new path with 90-degree segments
        return new paper.Path({
            segments: newSegments,
            closed: true,
            strokeColor: path.strokeColor,
            fillColor: path.fillColor,
            dashArray: path.dashArray,
            strokeScaling: path.strokeScaling,
        });
    }
    
    snapToGrid(point) {
        return new paper.Point(
            Math.round(point.x),
            Math.round(point.y)
        );
    }

    updatePathStyles() {
        const paths = this.renderedLayer.children;
        for (let i = 0; i < paths.length; i++) {
            if (paths[i] instanceof paper.Path) {
                paths[i].strokeColor = 'black';
                paths[i].fillColor = new paper.Color(0, 0, 0, 0.2);
                paths[i].dashArray = [5, 5];
                paths[i].strokeScaling = false;
            }
        }
    }

    animatePaths() {
        // Animate dash offset for selection paths
        const paths = this.renderedLayer.children;
        for (let i = 0; i < paths.length; i++) {
            if (paths[i] instanceof paper.Path) {
                paths[i].dashOffset -= 0.5;
            }
        }
        paper.view.update();
    }

    // Expose the paper.js instance to use all its functions
    get paper() {
        return paper;
    }

    // path.clone is buggy
    clonePath(originalPath) {
        const newPath = new paper.Path({
            strokeColor: originalPath.strokeColor,
            fillColor: originalPath.fillColor,
            strokeWidth: originalPath.strokeWidth,
            dashArray: originalPath.dashArray,
            dashOffset: originalPath.dashOffset,
            // Include other style properties as necessary
        });
    
        if (originalPath.segments && originalPath.segments.length) originalPath.segments.forEach(segment => {
            newPath.add(new paper.Segment(segment.point.clone(), segment.handleIn.clone(), segment.handleOut.clone()));
        });
    
        newPath.closed = originalPath.closed;
    
        // Copy transformations if any
        newPath.transform(originalPath.matrix);
    
        return newPath;
    }

    // Add the deselect method
    deselect() {
        // Clear all layers
        this.internalLayer.removeChildren();
        this.renderedLayer.removeChildren();
        this.selectionLayer.removeChildren();

        // Update the view
        paper.view.update();
    }

    invert() {
        // Create a rectangle that covers the entire document
        const documentBounds = new paper.Rectangle(0, 0, this.documentWidth/window.devicePixelRatio, this.documentHeight/window.devicePixelRatio)
        const documentPath = new paper.Path.Rectangle(documentBounds)

        // Combine all existing paths into a single compound path
        const existingPaths = this.internalLayer.children.filter(
            (item) => item !== documentPath
        );
                console.log("num paths",existingPaths.length)
        this.deselect()

        // Add the path to the internal layer
        this.internalLayer.activate()
        this.setPathStyle(documentPath)

      //  let newPath = documentPath.clone()
      //  newPath.style = documentPath.style
        this.mergePathAdd(documentPath)
        for(let i=0;i<existingPaths.length;i++) {
            console.log("sub", existingPaths[i])
            this.mergePathSubtract( existingPaths[i])
         }
        // Update the rendered paths
        this.updateRenderedPaths()

        // Update the view
        paper.view.update()
    }


    
    

}
