<svelte:options tag="fds-image-editor-selection"></svelte:options>

<script>
    import { onMount, onDestroy,afterUpdate } from 'svelte'
    import { LargePaper } from './largePaper.js'
    import paper from 'paper'
    // needed otherwise it will be not availabe in bundle
    // eslint-disable-next-line no-unused-vars
    import Dialog from './Dialog.svelte'
    let paperCanvas
    let largePaper

    // Document canvas size
    let documentWidth = 828; // Adjust as needed
    let documentHeight = 828;

    // Zoom and pan state
    let currentZoom = 1; // Scale factor

    let screen
    /**
     * the tool layer object
     * @type {object}
     */   
     export let tool_layer
    

    export function refresh() {
        initPaper()
        if (!largePaper) return
        if (globalThis.gyre.selectedTool && globalThis.gyre.selectedTool.subTool) {
            largePaper.setTool(globalThis.gyre.selectedTool.subTool,tool_layer.options)
        }
        mouseInfo.currentWidth=0
        updatePaperView()
      //  tool_layer.componentToolbar.refresh()

    }
    function initPaper() {   
        let canvas=globalThis.gyre.canvas             // canvas API
        currentZoom=canvas.zoom/100
        documentWidth=canvas.width
        documentHeight=canvas.height
        screen=canvas.screen
        screen=screen        
        if (!paperCanvas) return

        if (largePaper) {  
            return    
        }
        largePaper = new LargePaper(paperCanvas, documentWidth, documentHeight, currentZoom,callback)
        globalThis.paper=largePaper
        // Set initial tool to freeform
        //largePaper.setTool('freeform')

        // Initial transformations
        updatePaperView()

        // Render the view
        largePaper.paper.view.update()

    }
    function callback() {
        globalThis.gyre.undo_redo_add('selection', 'shape', {beforeShapesJSON:largePaper.beforeShapesJSON, afterShapesJSON:largePaper.afterShapesJSON }, (state, action) => {
            let restoreShapes
            if (action === 'undo') restoreShapes = state.data.beforeShapesJSON
            if (action === 'redo') restoreShapes = state.data.afterShapesJSON
            largePaper.restoreJSON(restoreShapes)
        })
    }
    onMount(() => {
        globalThis.gyre.callbackFunctions["copy"]=async () => {
            copyToClipboard()
         }
         globalThis.gyre.callbackFunctions["onKeyDown"]= async (e) => {
            if (e.keyCode===46) action("clear")
            if (e.keyCode===68 && e.ctrlKey) action("deselect")
            if (e.keyCode===37)  largePaper.moveAllPaths( new paper.Point(-1 , 0 ))
            if (e.keyCode===39)  largePaper.moveAllPaths( new paper.Point(1 , 0 ))
            if (e.keyCode===38)  largePaper.moveAllPaths( new paper.Point(0 , -1))
            if (e.keyCode===40)  largePaper.moveAllPaths( new paper.Point(0 , 1 ))

         }
        // Initialize LargePaper with the canvas element
        initPaper()
    })
	afterUpdate(() => {
        console.log("afterUpdate")
        initPaper()
	})
    onDestroy(() => {
        if (largePaper) {
            largePaper.paper.view.remove()
            largePaper = null
        }
        globalThis.gyre.callbackFunctions["copy"]=null
        globalThis.gyre.callbackFunctions["onKeyDown"]=null
    })



    // Update Paper.js view to match image transformations
    function updatePaperView() {
        if (largePaper) {
            const view = largePaper.paper.view
            view.viewSize = new paper.Size(screen.width, screen.height)
          //  view.matrix = new paper.Matrix(1, 0, 0, -1, 0, view.size.height);

            // Set the zoom
            view.zoom = currentZoom;
            view.center = new paper.Point(view.size.width / 2+globalThis.gyre.canvas.screen.left/currentZoom, view.size.height / 2+globalThis.gyre.canvas.screen.top/currentZoom);

//            view.center= new paper.Point(screen.width/2+globalThis.gyre.canvas.screen.left/currentZoom,screen.width/2+globalThis.gyre.canvas.screen.top/currentZoom)
           // largePaper.pan(-globalThis.gyre.canvas.screen.left,-globalThis.gyre.canvas.screen.top)
            view.update()

            largePaper.updateRenderedPaths()

        }
    }

async function crop() {
     // Get the image URL from globalThis
     const imageUrl = await globalThis.gyre.paletteValues.selectedLayer.renderInDocumentSize()
    if (!imageUrl) {
        console.log("No image URL found.")
        return;
    }
    // Create an off-screen canvas
    let hiddenCanvas = window.document.createElement('canvas')
    let ctx = hiddenCanvas.getContext('2d')
    // Load the image first
    const image = new Image()
    image.crossOrigin = "Anonymous" // To avoid CORS issues if needed    
    let boundingBox = null
    image.onload = async function () {
                // Get the device pixel ratio
                const scale = window.devicePixelRatio || 1
        // Calculate the bounding box based on unscaled paths from the internal layer
        largePaper.internalLayer.children.forEach(function (item) {
            if (!boundingBox) {
                boundingBox = item.bounds.clone() // Initialize with the first item's bounds
            } else {
                boundingBox = boundingBox.unite(item.bounds) // Expand to include each item's bounds
            }
        });
        if (!boundingBox) {
            console.log("No paths available to copy.")
            return;
        }
        // Set the canvas dimensions to match the bounding box, multiplied by scale
        let w=boundingBox.width * scale
        let h=boundingBox.height * scale
        hiddenCanvas.width = w
        hiddenCanvas.height = h
        ctx.drawImage(image, boundingBox.x * scale, boundingBox.y * scale,w,h,0,0,w,h)
        let imageLayer=globalThis.gyre.paletteValues.selectedLayer
        globalThis.gyre.canvas.change(w,h,100)
        imageLayer.url= hiddenCanvas.toDataURL()
        imageLayer.width=w
        imageLayer.height=h
        imageLayer.x=0
        imageLayer.y=0
        largePaper.moveAllPaths( new paper.Point(-boundingBox.x , -boundingBox.y ))
        await imageLayer.cacheReady()
        globalThis.gyre.refresh()
    }
    image.onerror = function () {
        console.error("Failed to load image from URL.")
    }
    image.src = imageUrl    
}

async function copyToClipboard() {
    // Get the image URL from globalThis
    const imageUrl = await globalThis.gyre.paletteValues.selectedLayer.renderInDocumentSize()
    if (!imageUrl) {
        console.log("No image URL found.");
        return;
    }
    // Create an off-screen canvas
    let hiddenCanvas = window.document.createElement('canvas');
    let ctx = hiddenCanvas.getContext('2d');
    // Load the image first
    const image = new Image();
    image.crossOrigin = "Anonymous"; // To avoid CORS issues if needed
    let boundingBox = null;

    // Wait for the image to load
    image.onload = function () {
        // Get the device pixel ratio
        const scale = window.devicePixelRatio || 1;
        // Calculate the bounding box based on unscaled paths from the internal layer
        largePaper.internalLayer.children.forEach(function (item) {
            if (!boundingBox) {
                boundingBox = item.bounds.clone(); // Initialize with the first item's bounds
            } else {
                boundingBox = boundingBox.unite(item.bounds); // Expand to include each item's bounds
            }
        });
        if (!boundingBox) {
            console.log("No paths available to copy.");
            return;
        }
        // Set the canvas dimensions to match the bounding box, multiplied by scale
        hiddenCanvas.width = boundingBox.width * scale;
        hiddenCanvas.height = boundingBox.height * scale;
     
        let selectedPaths = largePaper.internalLayer.children.map(item => largePaper.clonePath(item));

        // Iterate through each path to define the clipping mask
        const resultCanvas = window.document.createElement('canvas');
        resultCanvas.width=hiddenCanvas.width
        resultCanvas.height=hiddenCanvas.height
        const resultCtx = resultCanvas.getContext('2d');
        selectedPaths.forEach(path => {
            let hiddenCanvas = window.document.createElement('canvas');
            hiddenCanvas.width=resultCanvas.width
            hiddenCanvas.height=resultCanvas.height
            let ctx = hiddenCanvas.getContext('2d');
            // Translate the canvas to align with the bounding box top-left corner, multiplied by scale
            ctx.translate(-boundingBox.x * scale, -boundingBox.y * scale);
            ctx.clearRect(0, 0, hiddenCanvas.width, hiddenCanvas.height);

            ctx.save();
            ctx.beginPath();

            if (path instanceof paper.Path.Ellipse) {
                // Handle ellipse paths
                const rect = path.bounds;
                const center = rect.center;
                const radiusX = rect.width / 2;
                const radiusY = rect.height / 2;
                ctx.ellipse(
                    center.x * scale, 
                    center.y * scale, 
                    radiusX * scale, 
                    radiusY * scale, 
                    0, 0, 2 * Math.PI
                );
            } else {
                // Handle regular paths (including Bzier curves)
                const segments = path.segments;
                if (segments && segments.length > 0) {
                    // Move to the first point of the current path
                    ctx.moveTo(segments[0].point.x * scale, segments[0].point.y * scale);
                    for (let i = 0; i < segments.length; i++) {
                        const current = segments[i];
                        const next = segments[i + 1];
                        if (next) {
                            // Calculate control points
                            const cp1 = current.handleOut;
                            const cp2 = next.handleIn;
                            if (cp1.length > 0 || cp2.length > 0) {
                                // If there are handles, draw a Bzier curve
                                ctx.bezierCurveTo(
                                    (current.point.x + cp1.x) * scale,
                                    (current.point.y + cp1.y) * scale,
                                    (next.point.x + cp2.x) * scale,
                                    (next.point.y + cp2.y) * scale,
                                    next.point.x * scale,
                                    next.point.y * scale
                                );
                            } else {
                                // If no handles, draw a straight line
                                ctx.lineTo(next.point.x * scale, next.point.y * scale);
                            }
                        }
                    }

                    // Handle the closing segment manually to respect Bzier curves
                    if (path.closed) {
                        const lastSegment = segments[segments.length - 1];
                        const firstSegment = segments[0];
                        const cp1 = lastSegment.handleOut;
                        const cp2 = firstSegment.handleIn;

                        if (cp1.length > 0 || cp2.length > 0) {
                            // Draw a Bzier curve back to the first point
                            ctx.bezierCurveTo(
                                (lastSegment.point.x + cp1.x) * scale,
                                (lastSegment.point.y + cp1.y) * scale,
                                (firstSegment.point.x + cp2.x) * scale,
                                (firstSegment.point.y + cp2.y) * scale,
                                firstSegment.point.x * scale,
                                firstSegment.point.y * scale
                            );
                        } else {
                            // Draw a straight line back to the first point
                            ctx.lineTo(firstSegment.point.x * scale, firstSegment.point.y * scale);
                        }
                    }
                    // Do not call ctx.closePath() to avoid unintended closures
                }
            }
            ctx.closePath(); // Close the path after defining it

            ctx.clip();
            ctx.drawImage(image, 0, 0);
            resultCtx.drawImage(hiddenCanvas,0,0)   // this always works 
            //ctx.restore() 

        });

        resultCanvas.toBlob(async (blob) => {
            try {
            blob.uniqueID=123
                globalThis.gyre.paletteValues.internalCopyInfo={x:boundingBox.x * scale,y:boundingBox.y * scale,hash:await globalThis.gyre.clipboard.calculateImageDataHash(blob)}
                // Clipboard API supports writing the image
                await navigator.clipboard.write([
                    new ClipboardItem({
                        'image/png': blob
                    })
                ]);
            } catch (error) {
                console.error("Failed to copy image to clipboard:", error);
            }
        });
        // Clean up cloned paths
        selectedPaths.forEach(path => path.remove());
    }
    image.onerror = function () {
        console.error("Failed to load image from URL.");
    }
    image.src = imageUrl

}

export async function action(type) {
        if (!globalThis.gyre || !globalThis.gyre.canvas) return
        let gyre= globalThis.gyre
        if (!gyre.paletteValues.selectedLayer.url) return     
        if (type==="deselect") {
            largePaper.deselect()
        }           
        if (type==="invert") {
            largePaper.invert()
        }           
        if (type==="crop") {
            crop()
        }  
        if (type==="mask") {
            let maskURL=await generateMask(globalThis.gyre.paletteValues.smoothSelection)
           gyre.layerManager.addLayerMask(maskURL,true)
        }
        if (type==="clear") {
            let imageLayer=gyre.paletteValues.selectedLayer
            if (!imageLayer.url) return
            let maskURL=await generateMask(0)
            let transformedMaskURL=await imageLayer.extractImageFromDocument(maskURL)
            imageLayer.url = await gyre.imageAPI.processImage(imageLayer.url, (data, i, data2) => {
                if (data2) {
                    // If the pixel in imageUrl2 is white (R=255, G=255, B=255, A=255)
                    if (data2[i] === 255 && data2[i + 1] === 255 && data2[i + 2] === 255 && data2[i + 3] === 255) {
                        // Set the pixel in the original image (imageUrl) to fully transparent
                        data[i + 3] = 0;
                    }
                }
            }, transformedMaskURL)
            gyre.refresh()
        }        
        if (type==="inpainting_mask") {
            let maskURL=await generateMask(0)
            maskURL=await gyre.imageAPI.processImage(maskURL, (data, i) => {
                if (data[i] === 0 && data[i + 1] === 0 && data[i + 2] === 0) {
                    data[i + 3] = 0 // Make the pixel fully transparent
                }
            })
            gyre.maskManager.loadMask(maskURL)
        }
        if (type==="linked_layer_mask") {
            let newLayer = {            // linked clone
                type: 'image',
                name: " "+gyre.paletteValues.selectedLayer.name,
                ref: gyre.paletteValues.selectedLayer.id,
                visible: true
            }
            newLayer=gyre.createLayerInstance(newLayer.name, newLayer.type, newLayer)
            gyre.layerManager.addLayer(newLayer)    
            gyre.layerManager.selectLayers([newLayer.id])
            let maskURL=await generateMask(globalThis.gyre.paletteValues.smoothSelection)
            gyre.layerManager.addLayerMask(maskURL,true)
        }
        if (type==="add") {
            tool_layer.mode="add"
            largePaper.setMode("add")
            tool_layer.componentToolbar.refresh()
        }
        if (type==="subtract") {
            tool_layer.mode="subtract"
            largePaper.setMode("subtract")
            tool_layer.componentToolbar.refresh()
        }
        if (type==="move") {
            tool_layer.mode="move"
            largePaper.setMode("move")
            tool_layer.componentToolbar.refresh()
        }
        if (type==="fixed_ratio") {
            tool_layer.style="fixed_ratio"
            if (!tool_layer.ratio_width || !!tool_layer.ratio_height) {
                tool_layer.ratio_width=1
                tool_layer.ratio_height=1
                type="update_ratio"
            }
            tool_layer.componentToolbar.refresh()
        }
        if (type==="fixed_size") {
            tool_layer.style="fixed_size"
            tool_layer.componentToolbar.refresh()
        }        
        if (type==="normal") {
            tool_layer.style="normal"
            if (!tool_layer.options) tool_layer.options={}
            tool_layer.options.aspectRatio=tool_layer.options.fixedSize=null
            largePaper.setTool(globalThis.gyre.selectedTool.subTool,tool_layer.options)
            tool_layer.componentToolbar.refresh()
        }

        if (type==="update_ratio") {
            if (!tool_layer.options) tool_layer.options={}
            tool_layer.options.aspectRatio=null
            if (tool_layer.style==="fixed_ratio") {
                tool_layer.options.aspectRatio=tool_layer.ratio_width/tool_layer.ratio_height
            }
            largePaper.setTool(globalThis.gyre.selectedTool.subTool,tool_layer.options)
        }
        if (type==="update_fixed") {
            if (!tool_layer.options) tool_layer.options={}
            tool_layer.options.fixedSize=null
            if (tool_layer.style==="fixed_size" && parseInt(tool_layer.fixed_width)>0 && parseInt(tool_layer.fixed_height)>0) {
                tool_layer.options.fixedSize={width:parseInt(tool_layer.fixed_width),height:parseInt(tool_layer.fixed_height)}
            }
            largePaper.setTool(globalThis.gyre.selectedTool.subTool,tool_layer.options)
        }        
    }
    async function generateMask(softenEdges = 0) {
    // Get the image URL and scale from globalThis
    const imageUrl = await globalThis.gyre.paletteValues.selectedLayer.renderInDocumentSize();
    const scale = window.devicePixelRatio || 1;
    
    if (!imageUrl) {
        console.log("No image URL found.");
        return null;
    }

    // Get the paths from globalThis
    const paths = largePaper.internalLayer.children;
    if (paths.length === 0) {
        console.log("No paths available to generate mask.");
        return null;
    }

    // Create a canvas
    const canvas = window.document.createElement('canvas');
    const ctx = canvas.getContext('2d');

    // Load the image to get its dimensions
    const image = new Image();
    image.crossOrigin = "Anonymous";
    image.src = imageUrl;

    await new Promise((resolve) => {
        image.onload = resolve;
    });

    // Set canvas dimensions to match the image, considering the scale
    canvas.width = image.width;
    canvas.height = image.height;

    // Set background to black
    ctx.fillStyle = 'black';
    ctx.fillRect(0, 0, canvas.width, canvas.height);

    // Set fill color to white
    ctx.fillStyle = 'white';

    // Draw paths
    paths.forEach(path => {
        ctx.save();
        ctx.beginPath();

        if (path instanceof paper.Path.Ellipse) {
            const rect = path.bounds;
            const center = rect.center;
            const radiusX = rect.width / 2;
            const radiusY = rect.height / 2;
            ctx.ellipse(
                center.x * scale, 
                center.y * scale, 
                radiusX * scale, 
                radiusY * scale, 
                0, 0, 2 * Math.PI
            );
        } else {
            const segments = path.segments;
            if (segments && segments.length > 0) {
                ctx.moveTo(segments[0].point.x * scale, segments[0].point.y * scale);
                for (let i = 0; i < segments.length; i++) {
                    const current = segments[i];
                    const next = segments[i + 1];
                    if (next) {
                        const cp1 = current.handleOut;
                        const cp2 = next.handleIn;
                        if (cp1.length > 0 || cp2.length > 0) {
                            ctx.bezierCurveTo(
                                (current.point.x + cp1.x) * scale, (current.point.y + cp1.y) * scale,
                                (next.point.x + cp2.x) * scale, (next.point.y + cp2.y) * scale,
                                next.point.x * scale, next.point.y * scale
                            );
                        } else {
                            ctx.lineTo(next.point.x * scale, next.point.y * scale);
                        }
                    }
                }

                if (path.closed) {
                    const lastSegment = segments[segments.length - 1];
                    const firstSegment = segments[0];
                    const cp1 = lastSegment.handleOut;
                    const cp2 = firstSegment.handleIn;

                    if (cp1.length > 0 || cp2.length > 0) {
                        ctx.bezierCurveTo(
                            (lastSegment.point.x + cp1.x) * scale, (lastSegment.point.y + cp1.y) * scale,
                            (firstSegment.point.x + cp2.x) * scale, (firstSegment.point.y + cp2.y) * scale,
                            firstSegment.point.x * scale, firstSegment.point.y * scale
                        );
                    } else {
                        ctx.lineTo(firstSegment.point.x * scale, firstSegment.point.y * scale);
                    }
                }
            }
        }

        ctx.closePath();
        ctx.fill();
        ctx.restore();
    });

    // Apply edge softening if requested
    if (softenEdges > 0) {
        ctx.filter = `blur(${softenEdges}px)`;
        ctx.drawImage(canvas, 0, 0);
        ctx.filter = 'none';
    }

    // Convert canvas to data URL
    const dataURL = canvas.toDataURL('image/png');

    return  dataURL
}


let mouseInfo = {}
function mouseHandler(event) {
    mouseInfo.x = event.clientX + 20
    mouseInfo.y = event.clientY - 40
    mouseInfo.currentWidth=largePaper.currentWidth
    mouseInfo.currentHeight=largePaper.currentHeight
    mouseInfo.isDragging=largePaper.isDragging
    mouseInfo = mouseInfo
}
</script>
<style>
    .mouseInfo {
        border-radius: 5px;
        z-index: 1000;
        position: fixed;
        background-color: black;
        font-size: 11px;
        color: white;
        padding: 5px;
        width: 75px;
        height: 35px;
        opacity: 0.8;
    }    
    .canvasContainer {
        position:absolute;
     /*   border: 1px solid blue;    */    
    }
    canvas[resize] {
        width: 100%;
        height: 100%;
    }
</style>
{#if screen}

{#if largePaper && mouseInfo.isDragging}
<div class="mouseInfo" style="left:{mouseInfo.x}px;top:{mouseInfo.y}px;">
   <span class="w">w:</span>
    {mouseInfo.currentWidth} px<br />
    <span class="w">h:</span>
    {mouseInfo.currentHeight} px
</div>    
{/if} 
<div class="canvasContainer" style="left:{screen.left}px;top:{screen.top}px;width:{screen.width}px;height:{screen.height}px" on:mousemove={mouseHandler}
>
    <canvas bind:this={paperCanvas} resize="true"></canvas>
</div>
{/if}